import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import moment from 'moment';

import {
  ButtonBase, Grid, Table, TableBody,
  TableCell, TableHead, TableRow
} from '@material-ui/core';

import { withStyles } from '@material-ui/core/styles';
import colors from '../libs/styleguide/colors';
import Container from '../components/base-container';

@connect(({ betHistory }) => ({ betHistory }))
class BetHistory extends React.Component {

  render() {
    // eslint-disable-next-line
    const CustomTableCell = withStyles(theme => ({
      head: {
        fontSize: 12,
        color: colors.white,
      },
      body: {
        fontSize: 12,
        color: colors.white,
      },
    }))(TableCell);

    const styles = {
      icon: {
        verticalAlign: 'middle',
      },
      tabButton: {
        textAlign: 'center',
        height: 25,
        width: '100%',
        borderRadius: 4,
        border: 0,
        color: '#ffffff',
        backgroundColor: colors.darkGrey3,
      },
    };

    let { betHistory } = this.props;
    let data = betHistory.allBets.map(({ block_time, data }) => ({
      time: block_time,
      bettor: data.bettor,
      wager: data.wager,
      prize: data.roll_under < data.random_roll ? "-" : data.payout
    }));

    data.reverse();

    return (
      <div>
        <Container>
          <div>
            <img src="static/img/icon-game-star.svg" style={styles.icon} />
            <span>投注資訊</span>
          </div>
          <Grid container spacing={8}>
            <Grid item xs={4}><ButtonBase style={styles.tabButton}>所有投注</ButtonBase></Grid>
            <Grid item xs={4}><ButtonBase style={styles.tabButton}>最高獎金</ButtonBase></Grid>
            <Grid item xs={4}><ButtonBase style={styles.tabButton}>最高投注</ButtonBase></Grid>
          </Grid>
        </Container>
        <br />
        <Container>
          <Table>
            <TableHead>
              <TableRow>
                <CustomTableCell align="center">時間  </CustomTableCell>
                <CustomTableCell align="center">投注者</CustomTableCell>
                <CustomTableCell align="center">投注</CustomTableCell>
                <CustomTableCell align="center">獎金  </CustomTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {
                data.map(({ time, bettor, wager, prize }, index) => (
                  <TableRow key={index}>
                    <CustomTableCell align="center">{moment(time).format('hh:mm:ss')} </CustomTableCell>
                    <CustomTableCell align="center">{bettor} </CustomTableCell>
                    <CustomTableCell align="center">{wager} </CustomTableCell>
                    <CustomTableCell align="center">{prize} </CustomTableCell>
                  </TableRow>
                ))
              }
            </TableBody>
          </Table>
        </Container>
      </div>
    );
  }
}
BetHistory.propTypes = {
  betHistory: PropTypes.array
};

export default BetHistory;